import React from 'react';
import Link from 'next/link';
import './navbarMobile.css';
import { Box, Image } from 'rebass';
import { navbar_headers } from './navbar.json'

class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hidden: false,
            subheader: null
        }
        this.toggleNavbar = this.toggleNavbar.bind(this);
        this.showSubheader = this.showSubheader.bind(this);
        this.closeSubheader = this.closeSubheader.bind(this);
    }

    toggleNavbar() {
        this.setState({hidden: !this.state.hidden})
    }

    showSubheader(index) {
        this.setState({subheader: index})
    }

    closeSubheader() {
        this.setState({subheader: null})
    }

    render() {
        let mainHeaders = []
        let menus = []
        for (let i = 0; i < navbar_headers.length; i++) {
            mainHeaders.push(
                <button onClick={this.showSubheader(i)}>{navbar_headers[i].name}</button>
            )
            menus.push(
                <div className={(this.state.subheader === i) ? ('subheader-display') : ('subheader-hidden')}>

                </div>
            )
        }

        return (
            <div className='mobile-navbar'>
                {}
            </div>
        )
    }
}

export default Navbar;